.container {
    width: 100vw;
    padding: 10px;
    min-height: 80vh;
    color: #fff;
    display: flex;
    justify-content: space-between;
}

.left-subcontainer {
    padding: 10px;
}

#prashant-image {
    border-radius: 50%;
    box-shadow: 2px 10px 5px 0px rgba(52, 76, 183, 0.75);
}

.right-subcontainer {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#name-head {
    font-size: 45px;
    color: rgb(150, 3, 3);
}

#name-end {
    border-bottom: 2px solid rgb(218, 3, 3);
    position: relative;
    left: 40px;
}

.description {
    padding: 10px;
    transition: all ease 0.3s;
}

.hire {
    border-radius: 20px;
    border: 1px solid #c84b31;
    width: 180px;
    height: 40px;
    cursor: pointer;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hire:hover {
    background: rgb(13, 129, 206);
    color: #fff;
}

.hire a {
    text-decoration: none;
    border-radius: 25px;
    font-size: 30px;
    color: rgb(250, 15, 15);
}

.hire a:hover {
    color: #fff;
}

@media screen and (max-width: 991px) {
    .container {
        flex-direction: column;
    }
    .left-subcontainer {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #prashant-image {
        width: 60vw;
    }
}